.scroll-wrapper,
.market-limit,
.order-book,
.trade-history,
.depth-chart,
.open-orders .buy-sell-limit {
    position     : relative;
    margin-bottom: 10px;
}
.intro {
    z-index: 10;
}
.video-background {
    position: fixed;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100vh;
    opacity: 0.3;
    z-index: -2;
}
.intro-form-exchange {
    opacity: 0;
}
.order-icons {

    a {
        border-radius  : 100px;
        padding        : 7px 0px;
        margin-right   : 15px;
        height         : 25px;
        width          : 25px;
        display        : inline-flex;
        align-items    : center;
        justify-content: center;
        color          : $white;
        background     : $success;
        text-align     : center;
        &.both-order {
            background: linear-gradient(to bottom,  $success 0%,$success 50%, $danger 50%, $danger 100%);
        }
        &:hover {
            scale: 1.2;
        }
        transition: all 0.2s;
    }
}

.market-limit {
    .input-group-prepend {        
        label {
            padding: 10px 15px;
            font-size: 1rem;   
        }
    } 
}
.overlap {
    position: relative;
    left: -10px;
    top: -5px;
}
.price-pair,
.balance-widget,
.market-news,
.your-position {
    position     : relative;
    height       : 400px;
    margin-bottom: 10px;
}

.limit-nested {
    position: relative;
    height  : 210px;
}

.market-nested {
    position: relative;
    height  : 270px;
}

.stop-limit-nested {
    position: relative;
    height  : 165px;
}

.open-position-table {
    position     : relative;
    height       : 400px;
    margin-bottom: 10px;
}

.card-header {
        .nav-pills {
            line-height: initial;

            a {
                padding    : 0px 7px;
                color      : $body-color;
                font-weight: 500;

                &.active {
                    background-color: transparent !important;
                    color           : $primary !important;
                    opacity         : 1;
                }
            }
        }
}

.order-book-divider {
    text-align: center;
    padding   : 15px 0px;

    font-family: $barlow;

    h6 {
        margin-bottom: 0px;
    }
}

#chartdiv {
    width : 100%;
    height: 380px;
}






.duration-option {
    box-shadow: 0px 36px 48px rgba(31, 66, 135, 0.04);

    a {
        display      : inline-block;
        margin-left  : 10px;
        text-align   : center;
        padding      : 5px 15px;
        border       : 1px solid $border-color;
        // float     : left;
        transition   : all 0.3s ease;
        font-weight  : 500;
        border-radius: 5px;

        // color: $white;
        &       :hover,
        &:focus,
        &.active {
            background  : $primary;
            color       : $white;
            border-right: 1px solid transparent;
        }

        // &:first-child {
        //     border-top-left-radius   : 25px;
        //     border-bottom-left-radius: 25px;
        // }
        // &:last-child {
        //     border: 0px;
        // }
    }
}

.market-news {
    img {
        border-radius: 5px;
    }
}

.list-group-item {
    background-color: $dark;
}


